.background{
  background-image: url('../Assets/Images/gradient.png');
  background-repeat: 'no-repeat';
  background-size: 100% 100%;
  margin: 0;
}
.loginPage {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding-bottom: 10%;
  padding-top: 15%;
  width: 85%;
  margin: 0 auto;
}
.loginInput{
  padding-top: 0.75%;
  padding-bottom: 0.75%;
  padding-left: 1%;
  width: 100%;
  height: 3em;
  margin-bottom: 1em;
}

.loginInput div {
  width: 100% !important;
  height: 3em !important;
}

.loginInput div div {
  font-size: 1rem !important;
}

.loginInput div div div {
  width: 2.5% !important;
}

.makeStyles-labelInput-1 {
  font-size: 1rem !important;
}

.loginInput button {
  width: 100% !important;
  font-size: 1rem !important;
  height: 3em !important;
}

.loginInput button span span svg {
  font-size: 1rem !important;
}

.checkbox{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  justify-self: flex-start;
  width: 50% !important;
}

.checkbox span {
  padding: 0 !important;
}

.rememberMeForgotPassword{
  display: flex;
  flex-direction: row;
  font-size: 0.8rem !important;
  justify-content: space-between;
  font-family: 'Montserrat';
  padding-bottom: 3%;
  padding-left: 0.5%;
  align-items: center;
  width: 100% !important;
}

.landingSplash{
  background-color: #F6F2F6;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding-left: 10%;
  padding-bottom: 10%;
}
.header{
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  padding-bottom: 5%;
  padding-top: 10%;
  padding-right: 2%;
  width: 80%;
}
.headerTitle{
  padding-left: 5%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding-top: 2%;
}
.font{
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  font-family: 'Montserrat', sans-serif;  
  font-size: 20px;
  font-weight: 450;
}
.body{
  display: flex;
  justify-content: space-between;
  font-family: 'Montserrat', sans-serif;
  font-size: 42px;
  font-weight: 450;
  padding-bottom: 5%;
  width: 70%;
  padding-left: 1%;
  text-align: left;
}
.footer{
  display: flex;
}
.footerBody{
  font-family: 'Montserrat', sans-serif;
  font-size: 42px;
  font-weight: 450;
  width: 80%;
  padding-left: 1%;
  padding-bottom: 1%;
  text-align: left;
}
.TCWlogo{
  display: flex;
  align-self: center;
  width: 10em;
}
.TCWimage{
  display: flex;
  align-self: flex-start;
  width: 20em;
  max-width: 90%;
}
.arrowImage{
  display: flex;
  align-self: center;
  padding-right: 10%;
}
.arrowImage2{
  display: flex;
  align-self: center;
  padding-right: 17.7%;
  padding-bottom: 2%;
  padding-top: 1%;
}
.islandsImage{
  display: flex;
  align-self: flex-start;
  padding-right: 5%;
  width: 36%;
  height: 36%;
}
.loginArt{
  align-self: flex-start;
  padding-bottom: 2%;
  min-width: 10%;
  max-width: 15%;
  min-height: 10%;
  max-height: 15%;
  padding-bottom: 5%;
}
.TcwLogo2{
  align-self: flex-end;
  padding-right: 8%;
  min-width: 25%;
  max-width: 35%;
  min-height: 25%;
  max-height: 35%;
}

.link {
  text-decoration: none;
}

@media (max-width: 768px){
  .body,
  .footerBody {
    font-size: 24px;
  }
}

.eye-icon {
  width: 0.75em;
}